@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap');
*{
  font-family: 'Source Sans Pro', sans-serif;
}
@keyframes beat {
  0%{
    transform: scale(1);
  }
  50%{
    transform: scale(1.2);
  }
  100%{
    transform: scale(1);
  }
}
.App-header {
  background-color: #282c34;
  height: 4.4rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: fixed;
  top: 0;
  z-index: 9999;
}
nav{
  display: flex;
  margin-top: auto;
  margin-bottom: auto;
}
.App-header img{
  width: 7rem;
  margin-top: 0.5rem;
}



/* Main Section Styling */
.main{
  display: flex;
  position: absolute;
  top: 4.4rem;
  padding: 4% 10%;
  height: calc(100% - 4.4rem);
}
.preview{
  width: 100%;
}
.preview img{
  width: 100%;
}
.preview .time{
  position: absolute;
  top: 35%;
  left: 25%;
  /* transform: translate(-50%, -50%); */
  color: #fff;
  font-size: 3.4rem;
}
.preview .heartrate{
  position: absolute;
  top: 35%;
  left: 27.5%;
  /* transform: translate(-50%, -50%); */
  color: #fff;
  font-size: 3.4rem;
}
.preview .heartrate i{
  color: crimson;
  animation: beat infinite 1.2s;
}
.preview .heartrate p{
  margin: 0.35rem;
}
.details{
  width: 100%;
}
.details .desc{
  color: rgba(0,0,0,0.65);
  font-size: 20px;
  font-weight: 600;
}
.details h4{
  margin-top: 3rem;
  margin-bottom: 1rem;
  font-size: 22px;
  color: rgba(0, 0, 0, 0.8);
}
.details .selector{
  display: flex;
  flex-direction: row;
}
.details .selector img{
  width: 4.3rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}
.details .selector .selected{
  border: teal 2px solid;
  border-radius: 6px;
}
.details .btn-cont{
  display: flex;
}
.details .btn-cont .btn{
  min-width: 80px;
  background: gainsboro;
  border-radius: 8px;
  border: 0;
  outline: none;
  padding: 12px 24px;
  margin-right: 18px;
  font-weight: 600;
  cursor: pointer;
  box-sizing: content-box;
}
.details .btn-cont .selected{
  background: gray;
  color: #fff;
}
.details .primaryButton{
  color: #fff;
  background: teal;
  font-size: 20px;
  border-radius: 8px;
  border: 0;
  outline: none;
  padding: 8px 24px;
  margin-top: 1.4rem;
  cursor: pointer;
}

@media (max-width:500px) {
  .main{
    flex-direction: column;
    padding: 1.5rem;
    height: 100%;
  }
  .details{
    padding-bottom: 2rem;
  }
  .preview .time{
    top: 18%;
    left: 40%;
    /* transform: translate(-50%, -50%); */
    font-size: 2rem;
  }
  .preview .heartrate{
    top: 18%;
    left: 44%;
    /* transform: translate(-50%, -50%); */
    font-size: 2.2rem;
  }
  .preview .heartrate i{
    margin-left: 5px;
  }
  
}
@media(max-width:340px) {
  .preview .time{
    top: 22%;
  }
  .preview .heartrate{
    top: 23%;
  }
}